<template>
	<div class="card-content">
		<div class="head">
			<div :class="[classFn(),'title']">{{cardtit}}</div>
			<div class="datepicker">
				<el-date-picker type="month" format="yyyy.MM" v-model="modaldate" :clearable="false" placeholder="选择月" :picker-options="pickrange" @change='dateChange'></el-date-picker>
			</div>
		</div>
		<slot></slot>
	</div>
</template>

<script>
const prevMonth = new Date(new Date().setMonth((new Date().getMonth() - 1), 1));// 获取上一个月

export default {
  props: ['cardtit', 'tabletype', 'interface', 'themeid', 'modaldate'], // , 'modaldate'
  data () {
    return {
      // modaldate:prevMonth,
      origin: prevMonth.format('yyyy.MM'),
      currdate: prevMonth.format('yyyy.MM'),
      pickrange: {
        disabledDate (time) {
          if (time.getFullYear() > new Date().getFullYear()) {
            return true;
          } else if (time.getFullYear() == new Date().getFullYear()) {
            return (time.getMonth() > (new Date().getMonth() - 1));
          } else {
            return false;
          }
        }
      }
    };
  },
  methods: {
    dateChange (time) {
      // this.modaldate = time;
      // debugger
      this.$emit('update:modaldate', time);
      this.currdate = new Date(time).format('yyyy.MM');
      /* 暂时注释 */
      // const refresh = this.origin === this.currdate ? false : true;
      // 	this.origin = this.currdate;
      /* end */
      this.$emit('selectListen', {
        tabletype: this.tabletype,
        interface: this.interface,
        time: this.currdate,
        refresh: true
      });
    },
    classFn () {
      if (this.themeid == 2) {
        return 'color2';
      } else if (this.themeid == 3) {
        return 'color3';
      } else {
        return 'color1';
      }
    }
  },
  mounted () {
  }
};
</script>

<style scoped lang="less">
	.card-content{
		.title{
			color:#00F6FF;
			height:32px;
			line-height:32px;
			padding:0 30px;
			background: url("../../assets/images/inputs/input_tit_bg1.png") no-repeat center;
			background-size:100% 100%;
		}
		.title.color2{
			color:#9173FE;
			background: url("../../assets/images/inputs/input_tit_bg2.png") no-repeat center;
		}
		.title.color3{
			color:#1DE9B6;
			background: url("../../assets/images/inputs/input_tit_bg3.png") no-repeat center;
		}
	}
	.head{
		height:40px;
		position:relative;
		.flex-center();
		.ftSize(16px);
		.datepicker{
			.position(absolute,5px,0);
			cursor:pointer;
		}
	}
	.flex-center(@x:center,@y:center){
	 	display:flex;
	 	display:-webkit-flex;
	 	flex-flow:row nowrap;
	 	justify-content:@x;
	 	align-items:@y;
	}
	.ftSize(@size:12px){
		font-size:@size;
	}
	.position(@type:absolute,@top:0,@right:0,@bottom:auto,@left:auto){
		position:@type;
		top:@top;
		right:@right;
		bottom:@bottom;
		left:@left;
	}
</style>



